<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区域 -->
    <el-card>
      <!-- 搜索与添加区域 -->
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true">添加用户</el-button>
        </el-col>
      </el-row>
      <!-- 用户列表区域 -->
      <el-table :data="userlist" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="姓名" prop="username"> </el-table-column>
        <el-table-column label="邮箱" prop="email"> </el-table-column>
        <el-table-column label="电话" prop="mobile"> </el-table-column>
        <el-table-column label="角色" prop="role_name"> </el-table-column>
        <el-table-column label="状态">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)"></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="180px">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.id)"></el-button>
            <el-tooltip effect="dark" content="分配角色" placement="top" :enterable="false">
              <el-button type="warning" icon="el-icon-setting" size="mini" @click="showSetDialog(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!-- 添加用户对话框 -->
    <el-dialog title="添加用户" :visible.sync="addDialogVisible" width="70%" @close="addDialogClosed">
      <!-- 添加用户表单区 -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
        <el-form-item  label="用户名" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item  label="密码" prop="password">
          <el-input v-model="addForm.password" type="password"></el-input>
        </el-form-item>
        <el-form-item  label="邮箱" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item  label="手机" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUser">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改用户对话框 -->
    <el-dialog title="修改用户" :visible.sync="editDialogVisible" width="70%" @close="editDialogClosed">
      <!-- 修改用户表单区 -->
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
        <el-form-item  label="用户名">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item  label="邮箱" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item  label="手机" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editUserInfo">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 分配角色对话框 -->
    <el-dialog title="分配角色" :visible.sync="setDialogVisible" width="70%" @close="setDialogClosed">
      <el-form :model="userRoleList" ref="setFormRef" label-width="70px">
        <el-form-item  label="用户名">
          <el-input v-model="userRoleList.username" disabled></el-input>
        </el-form-item>
        <el-form-item  label="当前角色">
          <el-input v-model="userRoleList.curRole" disabled></el-input>
        </el-form-item>
        <el-form-item  label="变更角色">
          <el-select v-model="selectedRoleId" placeholder="请选择">
            <el-option v-for="item in rolesList" :key="item.id" :label="item.roleName" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveRoleInfo">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    /* 验证邮箱的自定义规则 */
    var checkEmail = (rule, value, cb) => {
      /* 验证邮箱的正则表达式 */
      const regEmail = /^([a-zA-Z]|[0-9])(\w)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/
      if (regEmail.test(value)) {
        /* 合法邮箱 */
        return cb()
      }
      cb(new Error('请输入合法的邮箱地址'))
    }
    /* 验证手机的自定义规则 */
    var checkMobile = (rele, value, cb) => {
      const regMobile = /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/
      if (regMobile.test(value)) {
        /* 正确的手机号 */
        return cb()
      }
      cb(new Error('请输入正确的手机号'))
    }
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      userlist: [],
      total: 0,
      /* 控制添加用户对话框的显示与隐藏 */
      addDialogVisible: false,
      /* 添加用户的表单数据 */
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''

      },
      /* 添加用户的表单验证规则 */
      addFormRules: {
        username: [
          { required: true, message: '请输入用户名称', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '长度在 3 到 10 个字符',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          {
            min: 6,
            max: 15,
            message: '长度在 6 到 15 个字符',
            trigger: 'blur'
          }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      /* 控制修改用户对话框的显示与隐藏 */
      editDialogVisible: false,
      /* 修改用户的信息 */
      editForm: {},
      /* 修改用户的表单验证规则 */
      editFormRules: {
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      /* 控制分配角色对话框的显示与隐藏 */
      setDialogVisible: false,
      /* 用户角色信息 */
      userRoleList: {
        username: '',
        curRole: '',
        id: ''
      },
      /* 所有角色信息 */
      rolesList: [],
      /* 选中更改的角色ID */
      selectedRoleId: ''
    }
  },
  methods: {
    async getUserList() {
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status === 200) {
        this.userlist = res.data.users
        this.total = res.data.total
      } else {
        this.$message.error('获取用户列表失败')
      }
    },
    /* 监听 pagesize 改变的事件 */
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    /* 监听 页码值 改变的事件 */
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    /* 监听 switch 开关状态的改变 */
    async userStateChanged(userinfo) {
      const { data: res } = await this.$http.put(
        `users/${userinfo.id}/state/${userinfo.mg_state}`
      )
      if (res.meta.status !== 200) {
        userinfo.mg_state = !userinfo.mg_state
        return this.$message.error('更新用户状态失败')
      }
      this.$message.success('更新用户状态成功')
    },
    /* 监听添加用户框的关闭事件 */
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    /* 提交添加用户数据表单 */
    addUser() {
      this.$refs.addFormRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post('users', this.addForm)
          if (res.meta.status !== 201) {
            this.$message.error('添加用户失败')
          } else {
            this.$message.success('添加用户成功')
            this.addDialogVisible = false
            this.getUserList()
          }
        }
      })
    },
    /* 展示编辑用户的对话框 */
    async showEditDialog(id) {
      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status !== 200) {
        this.$message.error('查询用户信息失败')
      } else {
        this.$message.success('查询用户信息成功')
        this.editForm = res.data
      }
      this.editDialogVisible = true
    },
    /* 提交修改用户数据表单 */
    editUserInfo() {
      this.$refs.editFormRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.put('users/' + this.editForm.id, {
            email: this.editForm.email,
            mobile: this.editForm.mobile
          })
          if (res.meta.status !== 200) {
            this.$message.error('修改用户失败')
          } else {
            this.$message.success('修改用户成功')
            this.editDialogVisible = false
            this.getUserList()
          }
        }
      })
    },
    /* 监听修改用户框的关闭事件 */
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    /* 根据ID删除对应的用户信息 */
    async removeUserById(id) {
      /* 弹框询问用户是否删除数据 */
      const res = await this.$confirm('此操作将永久删除该用户信息, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (res === 'confirm') {
        const { data: res } = await this.$http.delete('users/' + id)
        if (res.meta.status !== 200) {
          this.$message.error('删除用户失败')
        } else {
          this.getUserList()
          this.$message.success('删除用户成功')
          if (this.userlist.length === 1) {
            this.queryInfo.pagenum--
            this.getUserList()
          }
        }
      }
    },
    /* 展示分配角色的对话框 */
    async showSetDialog(role) {
      this.setDialogVisible = true
      this.userRoleList.username = role.username
      this.userRoleList.curRole = role.role_name
      this.userRoleList.id = role.id
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        this.$message.error('分配角色失败')
      }
      this.rolesList = res.data.filter(item => {
        return item.roleName !== role.role_name
      })
    },
    /* 提交分配角色的数据 */
    async saveRoleInfo() {
      if (!this.selectedRoleId) {
        return this.$message.error('请选择要更改的角色')
      }
      const uId = this.$refs.setFormRef.model.id
      const { data: res } = await this.$http.put('users/' + uId + '/role', {
        rid: this.selectedRoleId
      })
      if (res.meta.status !== 200) {
        this.$message.error('修改角色失败')
      }
      this.$message.success('修改角色成功')
      this.getUserList()
      this.setDialogVisible = false
    },
    /* 监听分配角色框的关闭事件 */
    setDialogClosed() {
      this.selectedRoleId = ''
    }
  },
  created() {
    this.getUserList()
  }
}
</script>

<style lang="less" scoped>
</style>
