<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区域 -->
    <el-card>
      <!-- 添加角色按钮区域 -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addDialogVisible = true">添加角色</el-button>
        </el-col>
      </el-row>
      <!-- 权限列表区域 -->
      <el-table :data="rolelist" border stripe>
        <el-table-column type="expand">
            <template slot-scope="scope">
                <el-row :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']" v-for="(item1, i1) in scope.row.children" :key="item1.id">
                    <!-- 渲染一级权限 -->
                    <el-col :span="5" :class="['vcenter']">
                        <el-tag closable @close="removeTagById(scope.row, item1.id)">{{item1.authName}}</el-tag>
                        <i class="el-icon-caret-right" :class="['vcenter']"></i>
                    </el-col>
                    <!-- 渲染二级和三级权限 -->
                    <el-col :span="19">
                        <el-row :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']" v-for="(item2, i2) in item1.children" :key="item2.id">
                            <el-col :span="6" :class="['vcenter']">
                                <el-tag type="success" closable @close="removeTagById(scope.row, item2.id)">{{item2.authName}}</el-tag>
                                <i class="el-icon-caret-right"></i>
                            </el-col>
                            <el-col :span="18">
                                <el-tag type="warning" v-for="item3 in item2.children" :key="item3.id" closable @close="removeTagById(scope.row, item3.id)">{{item3.authName}}</el-tag>
                            </el-col>
                        </el-row>
                    </el-col>
                </el-row>
            </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="角色名称" prop="roleName"> </el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"> </el-table-column>
        <el-table-column label="操作">
            <template slot-scope="scope">
                <el-row type="flex" class="row-bg" justify="space-between">
                    <el-col :span="6">
                        <el-tooltip effect="dark" content="编辑" placement="top" :enterable="false" >
                            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="6">
                        <el-tooltip effect="dark" content="删除" placement="top" :enterable="false">
                            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.id)"></el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="6">
                        <el-tooltip effect="dark" content="分配权限" placement="top" :enterable="false">
                            <el-button type="warning" icon="el-icon-setting" size="mini" @click="showSetDialogVisible(scope.row)"></el-button>
                        </el-tooltip>
                    </el-col>
                </el-row>
            </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 添加角色对话框 -->
    <el-dialog title="添加角色" :visible.sync="addDialogVisible" width="70%" @close="addDialogClosed">
      <el-form :model="addForm" ref="addRoleFormRef" label-width="70px">
        <el-form-item  label="角色名称">
          <el-input v-model="addForm.roleName"></el-input>
        </el-form-item>
        <el-form-item  label="角色描述">
          <el-input v-model="addForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addRole">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改角色对话框 -->
    <el-dialog title="修改角色" :visible.sync="editDialogVisible" width="70%" @close="editDialogClosed">
      <!-- 修改角色表单区 -->
      <el-form :model="editForm" ref="editRoleFormRef" label-width="70px">
        <el-form-item  label="角色名称">
          <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item  label="角色描述">
          <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editUserInfo">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 分配角色对话框 -->
    <el-dialog title="分配权限" :visible.sync="setDialogVisible" width="70%" @close="setDialogVisibleClosed">
      <el-tree :data="rightslist" ref="treeRef" :props="treeProps" show-checkbox default-expand-all node-key="id" :default-checked-keys="defKeys"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="allotRights">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rolelist: [],
      /* 控制添加角色对话框的显示与隐藏 */
      addDialogVisible: false,
      /* 添加角色的表单数据 */
      addForm: {
        roleName: '',
        roleDesc: ''
      },
      /* 控制修改角色对话框的显示与隐藏 */
      editDialogVisible: false,
      /* 修改角色的信息 */
      editForm: {},
      /* 控制分配权限对话框的显示与隐藏 */
      setDialogVisible: false,
      /* 所有权限数组 */
      rightslist: [],
      /* 树形控件的属性绑定对象 */
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      /* 默认选中的节点ID值数组 */
      defKeys: [],
      roleId: ''
    }
  },
  methods: {
    async getRoleList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status === 200) {
        this.rolelist = res.data
      } else {
        this.$message.error('获取角色列表失败')
      }
    },
    /* 监听修改角色框的关闭事件 */
    addDialogClosed() {
      this.addForm.rolename = ''
      this.addForm.roledesc = ''
    },
    /* 监听添加角色框的关闭事件 */
    editDialogClosed() {
      this.$refs.editRoleFormRef.resetFields()
    },
    /* 提交添加角色数据表单 */
    addRole() {
      this.$refs.addRoleFormRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post('roles', this.addForm)
          if (res.meta.status !== 201) {
            this.$message.error('添加用户失败')
          } else {
            this.$message.success('添加用户成功')
            this.addDialogVisible = false
            this.getRoleList()
          }
        }
      })
    },
    /* 展示编辑角色的对话框 */
    async showEditDialog(id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        this.$message.error('查询角色信息失败')
      } else {
        this.$message.success('查询角色信息成功')
        this.editForm = res.data
      }
      this.editDialogVisible = true
    },
    /* 提交修改角色数据表单 */
    editUserInfo() {
      this.$refs.editRoleFormRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.put('roles/' + this.editForm.roleId, {
            roleName: this.editForm.roleName,
            roleDesc: this.editForm.roleDesc
          })
          if (res.meta.status !== 200) {
            this.$message.error('修改用户失败')
          } else {
            this.$message.success('修改用户成功')
            this.editDialogVisible = false
            this.getRoleList()
          }
        }
      })
    },
    /* 根据ID删除对应的角色信息 */
    async removeUserById(id) {
      /* 弹框询问用户是否删除数据 */
      const res = await this.$confirm('此操作将永久删除该角色信息, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (res === 'confirm') {
        const { data: res } = await this.$http.delete('roles/' + id)
        if (res.meta.status !== 200) {
          this.$message.error('删除用户失败')
        } else {
          this.getRoleList()
          this.$message.success('删除用户成功')
        }
      }
    },
    /* 根据ID删除对应角色的权限 */
    async removeTagById(role, id) {
      /* 弹框询问用户是否删除数据 */
      const res = await this.$confirm('此操作将永久删除该角色权限, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (res === 'confirm') {
        const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${id}`)
        if (res.meta.status !== 200) {
          this.$message.error('删除权限失败')
        } else {
          this.$message.success('删除权限成功')
          role.children = res.data
        }
      }
    },
    /* 展示分配权限的对话框 */
    async showSetDialogVisible(role) {
      this.roleId = role.id
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status === 200) {
        this.rightslist = res.data
      } else {
        this.$message.error('获取权限列表失败')
      }
      this.getLeafKeys(role, this.defKeys)
      this.setDialogVisible = true
    },
    /* 通过递归获取角色下所有三级权限的id */
    getLeafKeys(node, arr) {
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item => this.getLeafKeys(item, arr))
    },
    /* 监听分配权限对话框关闭事件 */
    setDialogVisibleClosed() {
      this.defKeys = []
    },
    /* 提交分配权限树所选数据 */
    async allotRights() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post('roles/' + this.roleId + '/rights', {
        rids: idStr
      })
      if (res.meta.status !== 200) {
        this.$message.error('提交失败')
      } else {
        this.$message.success('提交成功')
        this.setDialogVisible = false
        this.getRoleList()
      }
    }
  },
  created() {
    this.getRoleList()
  }
}
</script>

<style lang="less" scoped>
.el-tag{
    margin: 7px;
}

.bdtop {
    border-top: 1px solid #eee;
}

.bdbottom {
    border-bottom: 1px solid #eee;
}

.vcenter {
    display: flex;
    align-items: center;
    justify-content: center;
}
</style>
